.item {
  display: flex;
  flex-direction: column;
  width: 260px;
  padding: 27px 0 0;
  margin: auto;
}

.item a {
  text-decoration: none;
}

.item__headline {
  font-size: 25px;
  line-height: 30px;
  margin: 0 0 5px;
  color: #000000;
}

.item__weekly {
  color: #63d1bb;
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  text-transform: uppercase;
  margin: 0 0 20px;
}

.item__description {
  font-size: 15px;
  line-height: 24px;
  color: #444444;
  margin: 0;
  font-weight: normal;
}

.item__features {
  margin: 15px 0 0;
  font-size: 15px;
  line-height: 20px;
  color: #444444;
  flex-direction: column;
  font-weight: normal;
}

.item__row {
  display: flex;
  justify-content: flex-start;
  padding: 10px 0;
  border-bottom: 1px solid #e5e5e5;
}

.item__row td {
  padding: 0;
  min-width: 95px;
}

.item__photo {
  margin: 27px 0 0;
  height: 315px;
}

.item__price {
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  font-weight: bold;
  margin: 20px auto 0;
}

.item__buy {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 17px;
  line-height: 24px;
  color: #444444;
  font-weight: bold;
  padding: 13px 83px;
  border: 2px solid #444444;
  margin: 24px 0 34px;

  &:focus,
  &:hover {
    background: #444444;
    color: white;
  }

  &:active {
    color: #8d8d8d;
  }
}

@media (min-width: @tablet-width) {
  .item {
    position: relative;
    margin: 0 auto;
    padding: 0 50px;
    width: 668px;
    height: min-content;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .item__headings {
    width: 100%;
    margin: 34px 0 47px -2px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .item__headline {
    display: flex;
    font-size: 43px;
    line-height: 50px;
    margin: 0 120px 0 0;
  }

  .item__weekly {
    font-size: 17px;
    line-height: 17px;
    width: 120px;
    height: 36px;
    padding: 10px 0 5px;
    margin: 0;
    text-align: center;
  }

  .item__description {
    font-size: 17px;
    line-height: 30px;
    margin: 0;
    max-width: 290px;
  }

  .item__features {
    width: 290px;
    margin: 35px 0 0;
  }

  .item__row {
    padding: 9px 0;
  }

  .item__photo {
    position: absolute;
    left: 385px;
    top: 141px;
    margin: 0;
    width: 333px;
    height: 404px;
  }

  .item__price {
    margin: 55px 0 35px 72px;
    font-size: 20px;
  }

  .item__buy {
    padding: 12px 99px;
    margin: 0;
  }
}

@media (min-width: @desktop-width) {
  .item {
    position: relative;
    margin: 52px auto 0;
    padding: 0 50px;
    width: 1050px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .item__headings {
    width: 100%;
    margin: 55px 0 45px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    z-index: 2;
  }

  .item__headline {
    font-size: 53px;
    line-height: 57px;
  }

  .item__headings a {
    order: 2;
  }

  .item__weekly {
    order: 1;
    padding: 13px 27px 0 22px;
    line-height: 17px;
    text-align: center;
  }

  .item__description {
    margin: 0 60px 0 175px;
  }

  .item__features {
    margin: 47px 0 0 175px;
  }

  .item__row {
    padding: 10px 0;
  }

  .item__photo {
    position: absolute;
    left: 50%;
    top: 2px;
    width: 525px;
    height: 636px;
  }

  .item__price {
    margin: 107px 0 33px 247px;
    font-size: 20px;
  }

  .item__buy {
    padding: 12px 99px;
    margin: 0 0 0 175px;
  }
}
