body {
  display: block;
  margin: 0;
  padding: 0;
  font-family: @font-family;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  width: 260px;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  font-display: optional;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}

@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  font-display: optional;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}

@media (min-width: @tablet-width) {
  .container {
    padding: 0 50px;
    width: calc(@tablet-width - 100px);
  }
}

@media (min-width: @desktop-width) {
  .container {
    padding: 0 50px;
    width: calc(@desktop-width - 100px);
  }
}
