body {
  display: block;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
}
.container {
  margin: 0 auto;
  padding: 0 30px;
  width: 260px;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  font-display: optional;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  font-display: optional;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
    width: calc(768px - 100px);
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0 50px;
    width: calc(1150px - 100px);
  }
}
.page-header {
  display: flex;
  width: 100%;
  flex-direction: column;
  border-top: 1px solid #ececec;
}
.page-header__logo {
  padding: 20px 0;
  display: flex;
  margin: auto;
  width: 86px;
  height: 35px;
}
.page-header__logo:focus,
.page-header__logo:hover {
  opacity: 0.5;
}
.page-header__logo:active {
  opacity: 0.2;
}
.page-header .container {
  padding: 0;
  min-width: 320px;
}
.offer-list,
.site-list,
.user-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.nav-note {
  font-size: 15px;
  line-height: 24px;
  font-weight: normal;
  color: #444444;
}
.nav-note,
.offer-list {
  display: none;
}
.site-list__item,
.user-list__item {
  border-top: 1px solid #ececec;
}
.offer-list__item a,
.site-list__item a,
.user-list__item a {
  display: flex;
  padding: 26px 0 28px 75px;
  text-decoration: none;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  color: #000000;
  cursor: pointer;
}
.offer-list__item a:focus,
.site-list__item a:focus,
.user-list__item a:focus,
.offer-list__item a:hover,
.site-list__item a:hover,
.user-list__item a:hover {
  color: #8d8d8d;
}
.offer-list__item a:active,
.site-list__item a:active,
.user-list__item a:active {
  color: #cacaca;
}
.user-list__item a {
  padding: 27px 0 27px 75px;
}
.user-list__search svg {
  margin-left: -43px;
  margin-top: 3px;
  position: absolute;
  fill: #d3d2d2;
}
.user-list__search svg:focus,
.user-list__search svg:hover {
  fill: #9ce2d4;
}
.user-list__search svg:active {
  fill: #d7f3ee;
}
.user-list__cart svg {
  margin-left: -46px;
  margin-top: 1px;
  position: absolute;
  fill: #d3d2d2;
}
.user-list__cart:focus svg,
.user-list__cart:hover svg {
  fill: #9ce2d4;
}
.user-list__cart:active svg {
  fill: #d7f3ee;
}
@media (min-width: 768px) {
  .page-header__logo {
    padding: 0;
    margin: 20px 0 0;
  }
  .page-header__logo-img {
    width: 113px;
    height: 46px;
  }
  .page-header {
    flex-direction: row;
  }
  .user-list {
    order: 1;
    flex-grow: 1;
    min-height: 87px;
  }
  .user-list span {
    display: none;
  }
  .user-list__item {
    border: none;
    border-left: 1px solid #ececec;
  }
  .user-list__item a {
    padding: 30px 0 30px 82px;
  }
  .user-list__search svg {
    margin-left: -48px;
    margin-top: 4px;
  }
  .user-list__cart svg {
    margin-left: -50px;
    margin-top: 2px;
  }
  .site-list {
    order: 2;
  }
  .site-list__item {
    border: none;
  }
  .site-list__item a {
    padding: 0;
    margin: 17px 38px 25px 0;
    font-size: 15px;
  }
  .nav-note {
    order: 3;
    display: flex;
    margin: 17px 0 0;
  }
}
@media (min-width: 1150px) {
