    order: 1;
    min-width: 400px;
  }
  .site-list__item a {
    margin: 30px 35px 25px 0;
    font-size: 17px;
  }
  .site-list__item:last-child a {
    margin: 30px 0 25px 35px;
  }
  .page-header__logo {
    order: 2;
    margin: 10px auto 0;
    width: auto;
    height: auto;
  }
  .page-header__logo-img {
    width: 148px;
    height: 73px;
  }
  .user-list {
    padding: 0 0 0 50px;
    min-width: 350px;
    order: 3;
  }
  .user-list__item a {
    padding: 30px 0 36px 83px;
  }
  .site-list__item:last-child {
    border: none;
    border-left: 1px solid #ececec;
  }
  .offer-list {
    order: 4;
    display: flex;
  }
  .nav-note {
    order: 5;
    margin: 16px 0 0;
  }
  .offer-list a {
    padding: 0;
    margin: 15px 60px 25px 0;
    font-size: 15px;
  }
}
.main-nav--closed .site-list,
.main-nav--closed .user-list {
  display: none;
}
.main-nav--nojs,
.main-nav--opened {
  display: block;
}
.main-nav__toggle {
  display: flex;
  border: none;
  background: #ffffff;
  padding: 35px 30px 48px;
  height: 14px;
  margin: -80px 0 0 240px;
}
.main-nav--closed .main-nav__toggle::before {
  display: block;
  content: "";
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}
.main-nav--opened .main-nav__toggle {
  padding: 42px 25px 41px;
}
.main-nav--opened .main-nav__toggle::after,
.main-nav--opened .main-nav__toggle::before {
  content: "";
  width: 30px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  margin: 0 0 0 -30px;
  transform: rotate(-45deg);
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--closed .page-header__logo,
.main-nav--closed .page-header__main-nav__toggle {
  display: flex;
}
.main-nav__wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-nav {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }
  .main-nav .main-nav__toggle {
    display: none;
  }
  .main-nav .site-list,
  .main-nav .user-list {
    display: flex;
    flex-direction: row;
    flex-grow: 0;
  }
}
.page-main {
