.catalog-making {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 0 58px;
  border: 1px solid #ececec;

  &:focus-within,
  &:hover {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  }

  &:active {
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  }
}

.catalog-making__headline-container {
  margin: 0;
  padding: 0;
  position: relative;
}

.catalog-making__icon {
  display: block;
  position: absolute;
  top: 30px;
  left: 34px;
  fill: #63d1bb;
}

.catalog-making__headline {
  width: 170px;
  margin: 0;
  padding: 25px 0 25px 90px;
  font-family: "OpenSans", sans-serif;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-align: left;
}

.catalog-making__video {
  position: relative;
  margin: 0;
  padding: 0 0 25px;
  width: 260px;
  height: 150px;
}

.catalog-making__video-button {
  display: block;
  margin: 0 auto;
  border: none;
  background-color: transparent;
  outline: none;
  position: absolute;
  top: 34px;
  left: 74px;

  &:focus,
  &:hover {
    transform: scale(1.2);
  }

  &:active {
    transform: scale(0.8);
  }
}

.catalog-making__video svg {
  position: absolute;
  top: 35px;
  left: 80px;
}

.catalog-making__description {
  padding: 0 37px 0 33px;
  margin: 0 0 25px;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  color: #000000;
  text-align: left;
}

.catalog-making__buy {
  display: block;
  margin: 0 34px 32px;
  padding: 16px 0;
  text-decoration: none;
  font-weight: bold;
  font-size: 17px;
  line-height: 16px;
  text-transform: uppercase;
  color: #444444;
  text-align: center;
  border: 2px solid #444444;

  &:focus,
  &:hover {
    background: #444444;
    color: white;
  }

  &:active {
    color: #8d8d8d;
  }
}

@media (min-width: @tablet-width) {
  .catalog-making {
    margin: 0 0 65px;
  }

  .catalog-making__icon {
    top: 32px;
    left: 41px;
  }

  .catalog-making__video-button {
    top: 185px;
    left: 275px;
  }

  .catalog-making__headline {
    font-size: 20px;
    width: auto;
    padding: 37px 0 33px 105px;
  }

  .catalog-making__description {
    padding: 0 45px 10px 40px;
    font-weight: 400;
    font-size: 15px;
    line-height: 30px;
  }

  .catalog-making__buy {
    margin: 0 40px 45px;
  }

  .catalog-making__video {
    order: -1;
    padding: 0;
    width: 668px;
    height: 455px;
  }
}

@media (min-width: @desktop-width) {
  .catalog-making {
    position: relative;
    flex-direction: row;
    flex-wrap: wrap;
    min-height: 455px;
    justify-content: flex-end;
    margin: 0 0 105px;
  }

  .catalog-making__headline-container {
    padding-left: 710px;
  }

  .catalog-making__headline {
    padding: 98px 150px 0 88px;
  }

  .catalog-making__icon {
    padding-left: 800px;
    left: 0;
    top: 40px;
  }

  .catalog-making__description {
    font-weight: 400;
    font-size: 15px;
    line-height: 30px;
    width: 235px;
    padding: 0 0 0 520px;
    margin: 5px 55px 0 0;
  }

  .catalog-making__video {
    position: absolute;
    left: 0;
    top: 0;
    width: 682px;
    height: 455px;
  }

  .catalog-making__buy {
    width: 230px;
    height: min-content;
    margin: 20px 55px 30px;
  }

  .catalog-making__video-button {
    top: 190px;
    left: 285px;
  }
}
